import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardHeader, Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { RECISAO, RECISAO_COMPLEMENTAR } from '../../../../../models/enumerated/mfp/tipoCalculoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, GrrfRV, REPORT_EVENT, REPORT_TITLE } from './grrfRV';

const DEFAULT_VALUES: GrrfRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  somenteConferencia: false,
  tipoCalculo: RECISAO.value,
  trabalhadores: [],
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const GuiaGrfcReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<GrrfRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  useEffect(() => {
    setTimeout(() => {
      if (globalReducer.globalParameter.empresa !== null && globalReducer.globalParameter.empresaMFP !== null) {
        const referencia = globalReducer.globalParameter.empresaMFP.referencia;
        formProps.setValue('referencia', referencia, true);
      }
    }, 800);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <WindowComponent
      title="Guia - GRFC"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <InputGroupInline
          formProps={{ ...formProps, validation: { required: true } }}
          id="referencia"
          placeholder="Informe a Referência"
          isPrependLabel={true}
          label="Referência"
          type={InputType.REFERENCE}
        />
        <Row>
          <Col style={{ marginBottom: '30px' }}>
            <Card>
              <CardHeader className="font-weight-bold bg-light">Tipo de Calculo</CardHeader>
              <CardBody>
                <Row style={{ marginBottom: '10px' }}>
                  <Col>
                    <CustomInput
                      id="tipoCalculoFolha"
                      type="radio"
                      label={RECISAO_COMPLEMENTAR.label}
                      name="tipoCalculo"
                      value={RECISAO_COMPLEMENTAR.value}
                      innerRef={formProps.register}
                    />
                  </Col>
                  <Col>
                    <CustomInput
                      id="tipoCalculoVale"
                      type="radio"
                      label={RECISAO.label}
                      name="tipoCalculo"
                      value={RECISAO.value}
                      innerRef={formProps.register}
                    />
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
        <VinculoIS
          id="trabalhadores"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Trabalhadores(s)"
          placeholder="Selecione Trabalhadore(s)"
          isPrependLabel={true}
          inputSearchProps={{ selectProps: { isMulti: true } }}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default GuiaGrfcReportView;
